.App {
  text-align: center;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: steelblue;
}


.header {
  width: 100%;
  height: 100px;
  background-color: steelblue;
  justify-content: center;
  position: fixed;
}

#hamWrapper {
  display: block;
  height: 75px;
  width: 75px; 
}

.ham-line {
  
  background-color: white;
  height: 2px;
  width: 45px;

 
}

#ham {
  position: absolute; 
  display: flex;
  justify-content: space-between;
  height: 37px;
  width: 45px;
  flex-direction: column;
  left: 18px;
  top: 25px;

}

#menu {
  position: absolute;
  display: flex;
  border-radius: 15px;
  background-color: steelblue;
  height: 400px;
  width: 300px;
  margin-top: 50px;
  margin-left: 15px;
  align-items: center;
  z-index: 1;
  
}

#menu p {
  color: white; 
}

#menu ul {
  list-style: none;
  font-size: 32px;
  padding-top: 50px;
  
}

#menu ul li {
  padding-bottom: 45px;
  color: white;
  

}

 .li :hover {
  
  color: gray; 
  cursor: pointer;
  
}

#boat {
  width: 100%;
  margin-top: 105px;
}

#rates {
  border: 2px solid black; 
  width: 75%;
  height: 30%;
  margin-top: 25px;
}

#button {
  background-color: green;
  color: white;
  border-radius: 15px;
  margin-top: 20px;
}

#testing {
  font-size: 150px;
  margin-top: 200px;
}

#booklesson {
  margin-top: 180px;
}

.inline {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

h2 {
  margin-bottom: 35px;
}

#aboutus {
  margin-top: 100px;
  width: 600px;
  font-size: larger;
  line-height: 2;
}

#thanks {
  margin-top: 200px;
  font-size: xx-large;
}

.booknow {
  
  margin-top: 50px;
  margin-bottom: 50px;
  
}

button {
  background-color: green; 
  color: white; 
  border-radius: 15px;
  height: 30px;
}

Link {
  text-decoration: none;
  list-style: none;
}












